import React from 'react';
import RcTree, { TreeNode as RcTreeNode } from 'rc-tree';
import CaretDownF from '@wind/icons/lib/icons/CaretDownF';
import FileO from '@wind/icons/lib/icons/FileO';
import LoadingO from '@wind/icons/lib/icons/LoadingO';
import PlusSquareO from '@wind/icons/lib/icons/PlusSquareO';
import MinusSquareO from '@wind/icons/lib/icons/MinusSquareO';
import animation from '../_util/openAnimation';
import classNames from 'classnames';
export class TreeNode extends React.Component {
    render() {
        return <TreeNode {...this.props}/>;
    }
}
export function renderSwitcherIcon(prefixCls, switcherIcon, showLine, { isLeaf, expanded, loading }) {
    if (loading) {
        return <LoadingO className={`${prefixCls}-switcher-loading-icon`}/>;
    }
    if (isLeaf) {
        return showLine ? <FileO className={`${prefixCls}-switcher-line-icon`}/> : null;
    }
    const switcherCls = `${prefixCls}-switcher-icon`;
    if (React.isValidElement(switcherIcon)) {
        return React.cloneElement(switcherIcon, {
            className: classNames(switcherIcon.props.className || '', switcherCls),
        });
    }
    if (switcherIcon) {
        return switcherIcon;
    }
    if (showLine) {
        return expanded ? (<MinusSquareO className={`${prefixCls}-switcher-line-icon f-fs-l`}/>) : (<PlusSquareO className={`${prefixCls}-switcher-line-icon f-fs-l`}/>);
    }
    return <CaretDownF className={classNames(switcherCls, 'f-fs')}/>;
}
// function getIcon(props: TreeNodeAttribute): React.ReactNode {
//   const { isLeaf, expanded } = props;
//   if (isLeaf) {
//     return <FileO />;
//   }
//   // return expanded ? <FolderOpenO /> : <FolderO />;
//   return null;
// }
export default class Tree extends React.Component {
    render() {
        const props = this.props;
        const { prefixCls, className, theme, switcherIcon, showLine } = props;
        let checkable = props.checkable;
        return (<RcTree 
        // icon={getIcon}
        switcherIcon={(nodeProps) => renderSwitcherIcon(prefixCls, switcherIcon, showLine, nodeProps)} {...props} className={classNames(className, {
            [`${prefixCls}-${theme}`]: !!theme
        })} checkable={checkable ? <span className={`${prefixCls}-checkbox-inner`}/> : checkable}>
        {this.props.children}
      </RcTree>);
    }
}
Tree.TreeNode = RcTreeNode;
Tree.defaultProps = {
    prefixCls: 'w-tree',
    checkable: false,
    showIcon: false,
    openAnimation: animation,
};
